import React, { forwardRef } from 'react';
import { Text as ChText } from "@chakra-ui/react";
import styled from '@emotion/styled'
import { responsive } from './ThemeProvider/theme';

const Text = styled(ChText)``

const TextNote = styled(Text)`
  position: relative;
  padding-left: 2.75em;
  &::before {
    position: absolute;
    left: 0;
    counter-increment: note-counter;
    content: "註" counter(note-counter) "：";
  }
`

Text.defaultProps = {
  fontSize: responsive('0.875em', '1em'),
}

Text.Inline = forwardRef((props, ref) => <Text as="span" {...props} ref={ref} />);
Text.Bold = forwardRef((props, ref) => <Text fontWeight="700" {...props} ref={ref} />);
Text.TitleDance = forwardRef((props, ref) => <Text as="span" color="dance" fontSize="1em" {...props} ref={ref} />);

Text.Note = forwardRef((props, ref) => (
  <TextNote
    display="inline-block"
    fontWeight="300"
    color="noteColor"
    fontSize={responsive('0.75em', '0.875em')}
    {...props}
    ref={ref}
  />
));
Text.H1 = forwardRef((props, ref) => <Text letterSpacing="wider" as="h1" fontSize={responsive('1.75em', '3.25em')} fontWeight="900" {...props} ref={ref} />);
Text.H2 = forwardRef((props, ref) => <Text as="h2" fontSize={responsive('1.375em', '1.75em')} fontWeight="900" {...props} ref={ref} />);
Text.H3 = forwardRef((props, ref) => <Text as="h3" fontSize={responsive('1.125em', '1.375em')} fontWeight="700" {...props} ref={ref} />);
Text.H4 = forwardRef((props, ref) => <Text as="h4" fontSize={responsive('1.125em', '1.25em')} fontWeight={responsive(700, 400)} {...props} ref={ref} />);

export default Text
