import React, { useMemo } from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import { responsive } from '../../../components/ThemeProvider/theme'

import { ChevDownButton } from '../../NextButton'

const Page = ({ fullpageApi, currentPage = 0, isMobile }) => {
  const isHome = useMemo(() => currentPage < 1, [currentPage])
  const isContent = useMemo(() => currentPage > 1, [currentPage])

  return useMemo(() => (
    <Box
      position="absolute"
      left={responsive('1.25em', '4em')}
      right="1.25em"
      bottom={isHome ? 0 : '50%'}
      transform={`translateY(${isHome
        ? `calc(100% - ${isMobile ? '9em' : '16em'})`
        : (isContent ? 'calc(50% - 100vh)' : '50%')})`}
      transition="transform 1s, bottom 1s"
    >
      <Box
        display={responsive('block', 'inline-block')}
        textAlign="center"
        mx={responsive('auto', 0)}
      >
        <Text letterSpacing={responsive('wide', 'widest')} fontSize={responsive('0.875em', '1.375em')}>兩廳院售票系統 x OPENTIX 兩廳院文化生活</Text>
        <Text.H1 letterSpacing="0.1em" color="gray.100">2020年消費行為報告</Text.H1>
        {!isMobile && (
          <Box position="relative" zIndex={1}>
            <ChevDownButton
              opacity={Number(isHome)}
              onClick={() => isHome && fullpageApi.moveSectionDown()}
              pointerEvents={isHome ? 'all' : 'none'}
            >
              進入報告
            </ChevDownButton>
          </Box>
        )}
        <Box opacity={Number(!isHome)} transition="opacity 1s" textAlign="justify" maxWidth="36em" mt={responsive(0, '-4em')}>
          <Text my="2em">
            2020年隨著嚴重特殊傳染性肺炎影響全球甚鉅，國內上半年藝文活動幾近停擺，面對嚴峻環境挑戰，表演藝術產業在這段時間整裝待發、蓄儲未來能量，所幸台灣防疫措施嚴密管控，得以在下半年領先國際解封開演。
            <br />
            <br />
            本次委託國立陽明交通大學統計學研究所統計2018~2020年表演藝術銷售數據，期待透過數據，提供藝文夥伴多元的思考與正面能量。
          </Text>
          <Text.Note fontWeight="300">
            資料來源：兩廳院售票平台2018~2020年及OPENTIX 2020年演出節目銷售與購票會員資料；未涵蓋非售票的藝文活動。
          </Text.Note>
          {!isMobile &&
            <Text.Note fontWeight="300">
              本報告針對大量節目、團隊與消費者之統計分析，其結果能呈現總體平均行為，不代表各別團隊、消費者或特定節目情形，分析結論對整體產業代表性僅供參考。
            </Text.Note>
          }
        </Box>
      </Box>

    </Box>
  ), [isHome, isContent, isMobile, fullpageApi])
}

export default Page
