import React, { memo, useMemo } from 'react'
import PropTypes from 'prop-types'
import { isArray } from 'lodash';
import { useMeasure } from 'react-use';

import Box from '../components/Box';
import theme from '../components/ThemeProvider/theme';
// import headerContext from '../contexts/header/context'

import Header from './Header'
import Bg from './Bg';

const Layout = ({ children, isMobile, active, getFullpageApi, bg, currentPage }) => {
  const bgConfig = useMemo(() => isArray(bg) ? bg[isMobile ? 0 : 1] : bg, [bg, isMobile])
  const [mesureRef, { height }] = useMeasure()
  return (
    <Bg isMobile={isMobile} variant={bgConfig}>
      <Header currentPage={currentPage} getFullpageApi={getFullpageApi} height={theme.headerHeight} active={active} bodyHeight={height} />
      <Box height="100%" position="relative" ref={mesureRef}>
        {children}
      </Box>
    </Bg>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default memo(Layout)
