import React, { useMemo } from 'react'
import { useMeasure } from 'react-use';

import Box from '../../components/Box'
import { responsive } from '../../components/ThemeProvider/theme';
import useWebpImage from '../../components/utils/useWebpImage';

const web = [require('./web_bg.webp').default, require('./web_bg.png').default]
const web2 = [require('./web_bg2.webp').default, require('./web_bg2.png').default]
const mobile1 = [require('./mobile_bg1.webp').default, require('./mobile_bg1.png').default]
const mobile2 = [require('./mobile_bg2.webp').default, require('./mobile_bg2.png').default]

const Bg = ({ variant, ...props }) => {
  const webBg = useWebpImage(web)
  const webBg2 = useWebpImage(web2)
  const mobile1Bg = useWebpImage(mobile1)
  const mobile2Bg = useWebpImage(mobile2)
  const [measureRef, { width, height }] = useMeasure()
  const isVertical = useMemo(() => height / width > 1.5, [width, height])
  const variation = useMemo(() => {
    const full = {
      backgroundSize: isVertical ? `auto 125%` : '100%',
      backgroundPosition: `${isVertical ? width * -0.6 : 0}px 50%`,
    }
    const side = {
      backgroundSize: isVertical ? `auto 140%` : '100%',
      backgroundPosition: `${width * (isVertical ? -0.2 : 0.2)}px 50%`,
    }
    const fade =  {
      ...side,
      opacity: 0.3,
    }
    const noneSide =  {
      ...side,
      opacity: 0,
    }
    const none =  {
      ...full,
      opacity: 0,
    }
    const gray = {
      ...full,
      opacity: 0.5,
      backgroundImage: `url(${mobile2Bg})`,
    }
    return {
      full,
      side,
      fade,
      noneSide,
      none,
      fullFade: {
        ...full,
        opacity: 0.5,
      },
      gray,
      noneGray: {
        ...gray,
        opacity: 0,
      },
      thanks: noneSide,
    }
  }, [width, mobile2Bg, isVertical])
  return (
    <Box
      height="100%"
      _before={{
        content: responsive('none', '""'),
        position: 'absolute',
        display: 'block',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        backgroundRepeat: 'no-repeat',
        transition: 'all 1s',
        opacity: Number(variant === 'thanks'),
        backgroundImage: `url(${webBg2})`,
        backgroundPosition: `0px 100%`,
        backgroundSize: '100%',
        transform: `translateY(${100 * (1 - Number(variant === 'thanks'))}%)`
      }}
    >
      <Box
        height="100%"
        position="relative"
        ref={measureRef}
        _before={{
          content: width > 0 ? '""' : null,
          position: 'absolute',
          display: 'block',
          left: 0,
          right: 0,
          top: 0,
          bottom: 0,
          backgroundImage: `url(${isVertical ? mobile1Bg : webBg})`,
          backgroundRepeat: 'no-repeat',
          transition: 'all 1s',
          ...variation[variant]
        }}
        {...props}
      />
    </Box>
  )
}

export default Bg
