import React, { memo, useMemo } from 'react'
import {
  IconButton,
  Image,
  Drawer,
  DrawerBody,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Stack,
  Link,
} from "@chakra-ui/react";
import { inRange } from 'lodash';
import { FiMenu } from 'react-icons/fi'

import Box from '../components/Box';
import Button from '../components/Button';
import Flex from '../components/Flex';
import { responsive } from '../components/ThemeProvider/theme';

import logo from './logo.svg'

const links = [
  {
    label: '首頁',
    link: 1
  },
  {
    label: '總體產值',
    link: 3
  },
  {
    label: '2020產值變化',
    link: 5
  },
  {
    label: '觀眾購票行為與人氣節目',
    link: 8
  },
  {
    label: '藝FUN券振興效果',
    link: 14
  },
  {
    label: '下載完整報告',
    link: 15
  },
]

const DrawerMenu = memo(({ getFullpageApi, currentPage }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const menuItems = useMemo(() => links.map(({ label, link }, i, { length }) => (
    <Box key={i}>
      <Button.Transparent
        border="none"
        height="auto"
        color={i === length - 1 ? currentPage + 1 === link && 'dance' : inRange(currentPage + 1, link, links[i + 1].link) && 'dance'}
        onClick={() => {
          onClose()
          getFullpageApi().moveTo(link)
        }}
        _focus={{
          outline: 'none',
        }}
        _active={{
          bg: 'transparent'
        }}
      >{label}</Button.Transparent>
    </Box>
  )), [currentPage, getFullpageApi, onClose])
  return (
    <>
      <IconButton
        variant="ghost"
        colorScheme="white"
        icon={<FiMenu size="2em" />}
        onClick={onOpen}
      />
      <Drawer
        isOpen={isOpen}
        onClose={onClose}
        isFullHeight
        blockScrollOnMount={false}
      >
        <DrawerOverlay />
        <DrawerContent
          bg="linear-gradient(270deg, #242424 0%, #474747 53.13%, #5C5C5C 100%)"
          box-shadow="0px 5px 10px 4px rgba(255, 255, 255, 0.2)"
        >
          <DrawerCloseButton size="lg" fontSize="1.25em" color="white"/>
          <DrawerBody height="100%">
            <Stack spacing={responsive('1.5rem', '1.75rem')} justifyContent="center" height="100%">
              {menuItems}
            </Stack>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  )
})

const Header = ({ active, bodyHeight, getFullpageApi, currentPage, ...props }) => {

  return (
    <Flex
      position="fixed"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent={responsive('flex-end', 'space-between')}
      zIndex="docked"
      px={responsive('1rem', '2em')}
      _before={{
        content: '""',
        position: 'absolute',
        display: 'block',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        bgImage: 'linear-gradient(180deg, #000000 0%, #5C5C5C 100%)',
        opacity: Number(active),
        transition: 'opacity 0.75s',
      }}
      {...props}
    >
      <Box
        position={responsive('absolute', 'relative')}
        left={responsive('50%', 'auto')}
        transform={responsive(`translate(-50%, ${active ? '0' : `calc(${bodyHeight}px - 14em)`}) scale(${active ? 0.85 : 1})`, 'none')}
        transition="all 1s"
        width="138px"
      >
        <Link href="https://www.opentix.life" isExternal>
          <Image src={logo}  />
        </Link>
      </Box>
      <DrawerMenu currentPage={currentPage} getFullpageApi={getFullpageApi} />
    </Flex>
  )
}

export default memo(Header)
