import { theme, extendTheme } from "@chakra-ui/react";
import { createBreakpoints } from "@chakra-ui/theme-tools"
import memoizeOne from 'memoize-one';
import get from 'lodash/get';

const chakraBp = createBreakpoints({
  sm: "30em",
  md: "48em",
  lg: "60em",
  xl: "80em",
  "2xl": "96em",
})

export const breakpoints = Object.values(chakraBp).slice(1)

export const responsiveIndex = [
  [1, 'mobile'],
  [2, 'tablet'],
  [3, 'laptop'],
  [4, 'desktop'],
]

const responsiveMap = breakpoints.map((_, i) => {
  const id = responsiveIndex.findIndex(([ri]) => ri > i)
  return id >= 0 ? id : responsiveIndex.length
})

export const responsive = memoizeOne((...args) => {
  const argsLen = args.length
  if (argsLen <= 1) return args[0]
  return breakpoints.map((_, i) => get(args, [responsiveMap[i]], null))
});

export const mobileOrDesktop = responsive

export const containerWidth = '81.25em'
export const containerPadding = responsive('1.25em', '6.25em')

const font = '"Noto Sans TC", "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';
const fonts = {
  heading: font,
  body: font,
  mono: "Menlo, monospace",
}

const primary = 'blue'
const secondary = 'green'
const danger = 'red'
const noteColor = '#DADADA'
const circleNoteColor = '#F6F6F6'
const drama = '#E05B5A'
const music = '#ECA687'
const dance = '#FAF599'
const family = '#9DC29F'
const other = '#39868C'
export const colorScale = [
  drama,
  music,
  dance,
  family,
  other,
  '#a7a7a7',
]

const overrides = {
  fonts,
  colors: {
    ...theme.colors,
    primary: get(theme.colors, `${primary}.500`),
    secondary: get(theme.colors, `${secondary}.500`),
    danger: get(theme.colors, `${danger}.500`),
    text: get(theme.colors, 'black'),
    drama,
    music,
    dance,
    family,
    other,
    noteColor,
    circleNoteColor,
  },
  breakpoints,
  containerWidth,
  headerHeight: responsive('48px', '60px'),
}

const customTheme = extendTheme(overrides)

export default customTheme
