import React, { createElement, useMemo, useRef, forwardRef, useEffect } from 'react'
import ReactFullpage from '@fullpage/react-fullpage';
import { range } from 'lodash';
import { useHash } from 'react-use';
import styled from '@emotion/styled';

import useResponsive from '../contexts/mediaQuery/useResponsive'
import Container from '../components/Container';
import Box from '../components/Box';

import Layout from './Layout'
import Page1 from './Pages/1'
import NextButton from './NextButton'

const extractPage = (hash) => Math.max(hash.replace(/\D+/, '') - 1, 0)

const Page = styled(Container)`
counter-reset: note-counter;
`

const pages = [
  { comp: 'div', bg: 'full', next: '進入報告' },
  { comp: 'div', bg: 'side', next: '總體產值' },
  { comp: require('./Pages/3/Loadable').default, bg: ['noneSide', 'fade'], next: '　' },
  { comp: require('./Pages/4/Loadable').default, bg: ['noneGray', 'noneSide'], next: '2020產值變化' },
  { comp: require('./Pages/5/Loadable').default, bg: ['gray', 'side'], next: '　' },
  { comp: require('./Pages/6/Loadable').default, bg: ['noneGray', 'noneSide'], next: '　' },
  { comp: require('./Pages/6/Loadable').default, bg: ['none', 'noneSide'], next: '觀眾購票行為與人氣節目', dataKey: 1 },
  { comp: require('./Pages/8/Loadable').default, bg: ['none', 'fade'], next: '　' },
  ...range(4).map(d => ({ comp: require('./Pages/9/Loadable').default, bg: ['none', 'noneSide'], next: '　', dataKey: d })),
  { comp: require('./Pages/13/Loadable').default, bg: ['none', 'noneSide'], next: '藝FUN券振興效果' },
  { comp: require('./Pages/14/Loadable').default, bg: ['none', 'fade'], next: '　' },
  { comp: require('./Pages/15/Loadable').default, bg: ['fullFade', 'thanks'] },
]

const FullpageJS = forwardRef(({ isMobile }, ref) => {
  return useMemo(() => (
    <ReactFullpage
      //fullpage options
      licenseKey={process.env.REACT_APP_FULLPAGE_KEY}
      scrollingSpeed={1000}
      // scrollOverflow
      // autoScrolling={isDesktop}
      // autoScrolling={false}
      render={({ fullpageApi }) => {
        ref.current = fullpageApi
        return (
          <ReactFullpage.Wrapper>
            {pages.map((p, i) => (
              <section
                className="section"
                data-anchor={`page${i + 1}`}
                key={i}
              >
                <Page>
                  {createElement(p.comp, {
                    fullpageApi,
                    isMobile,
                    dataKey: p.dataKey
                  })}
                </Page>
              </section>
            ))}
          </ReactFullpage.Wrapper>
        )
      }}
      normalScrollElements=".leaderboard"
    />
  ), [isMobile])
})

const HomePage = () => {
  const [hash] = useHash()
  const fpApi = useRef()

  useEffect(() => {
    if (fpApi.current) {
      window.gtag('event', 'page_view', {
        page_path: window.location.pathname + window.location.hash
      })
    }
  }, [hash])
  const currentPage = useMemo(() => extractPage(hash), [hash])
  const { isMobile } = useResponsive()
  const hasNext = Boolean(pages[currentPage]?.next)
  return (
    <Layout
      currentPage={currentPage}
      getFullpageApi={() => fpApi.current}
      isMobile={isMobile}
      active={currentPage > 0}
      bg={pages[currentPage]?.bg}
    >
      <FullpageJS
        isMobile={isMobile}
        ref={fpApi}
      />
      <Box.FullAbs pointerEvents="none">
        <Page position="relative" height="100%">
          <Page1 currentPage={currentPage} fullpageApi={fpApi.current} isMobile={isMobile} />
        </Page>
      </Box.FullAbs>
      <NextButton
        isDisabled={!hasNext}
        pointerEvents={hasNext ? 'all' : 'none'}
        opacity={Number(hasNext && (isMobile || currentPage > 0))}
        onClick={() => fpApi.current.moveSectionDown()}
      >
        {pages[currentPage].next}
      </NextButton>
    </Layout>
  )
}

export default HomePage
