import React from 'react'
import { Button } from '@chakra-ui/button'
import { BsChevronDown } from 'react-icons/bs'
import { AspectRatio } from '@chakra-ui/layout'
import { motion } from 'framer-motion'

import Box from '../../components/Box'

export const ChevDownButton = ({ children, ...props }) => (
  <Button
    mt="2em"
    flexDirection="column"
    fontSize="0.875em"
    variant="ghost"
    colorScheme="whiteAlpha"
    color="white"
    _hover={{bg: 'transparent' }}
    fontWeight="400"
    height={children !== '' ? 'auto' : '2em'}
    {...props}
  >
    {children}
    <motion.div
      animate={{ y: [2, 10, 2] }}
      transition={{ duration: 1, repeat: Infinity }}
    >
      <BsChevronDown size="1.5em" />
    </motion.div>
  </Button>
)

const NextButton = ({ children, onClick, isDisabled, ...props }) => {
  return (
    <Box.Absolute
      transition="opacity 1s"
      left="50%"
      bottom="0"
      transform="translateX(-50%)"
      pb="1em"
      width="12em"
      textAlign="center"
      {...props}
    >
      <Box position="absolute" width="100%" top="2em">
        <AspectRatio ratio={1}><Box bgImage="radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%)" /></AspectRatio>
        <AspectRatio ratio={1}><Box bgImage="radial-gradient(50% 50% at 50% 50%, rgba(255, 246, 159, 0.25) 0%, rgba(255, 249, 196, 0.02) 81.25%)" /></AspectRatio>
      </Box>
      <ChevDownButton isDisabled={isDisabled} onClick={onClick}>{children}</ChevDownButton>
    </Box.Absolute>
  )
}

export default NextButton
